<script setup>
    import { onMounted, ref } from 'vue';
    import store from '../../store';
    import formatMoney from '../../utils/formatMoney';
    import post from '../../utils/post';
    import Button from './../../components/Button/';

    const inputInvestment = ref('');

    async function investHandler(){
        const value = parseInt(Math.abs(inputInvestment.value));
        if(value && store.state.initial.balance >= value){
            const res = await post('Invest', { value });
            if(res){
                inputInvestment.value = '';
                store.state.initial.balance -= value;
                store.state.initial.Investments.deposit += value;
            }
        }
    }
    
    async function redeemInvestment(){
        const res = await post('InvestRescue');
        if(res){
            store.state.initial.balance += (store.state.initial.Investments.total + store.state.initial.Investments.deposit);
            store.state.initial.Investments.total = 0;
            store.state.initial.Investments.liquid = 0;
            store.state.initial.Investments.brute = 0;
            store.state.initial.Investments.deposit = 0;
        }
    }
</script>


<template>
    <div class="chart">
        <p class="title">Investimentos</p>
        <div class="main-chart">
            <div class="left-main-chart">
                <div class="subtitle">Investimentos disponíveis</div>
                <div class="slide-frame">
                    <div class="header-slide-frame">
                        CDBs
                        <img src="./images/signal.svg" />
                    </div>
                    <div class="text-slide-frame">
                        É uma importante fonte de captação de dinheiro para as instituições financeiras e uma das mais comuns para a pessoa física.
                    </div>
                    <div class="input-frame">
                        <input type="number" v-model="inputInvestment" placeholder="0,00" />
                        <Button :cb="investHandler" class="button-input" text="INVESTIR" />
                    </div>
                </div>
            </div>
            <div class="right-main-chart">
                <p class="title-right-main-chart">Seus rendimentos</p>
                <p class="subtitle-right-main-chart">Seus investimentos renderam</p>
                <span class="value-right-main-chart">{{formatMoney(store.state.initial.Investments?.total)}}</span>
                <p class="subdesc-right-main-chart">Até o atual momento</p>
                <div class="income-container">
                    <div class="income-frame">
                        <p>Rendimentos brutos</p>
                        <span>{{formatMoney(store.state.initial.Investments?.brute)}}</span>
                    </div>
                    <div class="income-frame">
                        <p>Rendimentos líquidos</p>
                        <span>{{formatMoney(store.state.initial.Investments?.liquid)}}</span>
                    </div>
                </div>
                <div class="deposited-amounts">
                    <div class="deposited-frame">
                        <p>Valor Aplicado</p>
                        <span>{{formatMoney(store.state.initial.Investments?.deposit)}}</span>
                    </div>
                </div>
                <Button :cb="redeemInvestment" class="redeem" text="RESGATAR"></Button>
            </div>
        </div>
    </div>
</template>

